import React from 'react';
import { FormattedMessage } from 'react-intl';
import qrCode from '../static/qr.png';

interface PropProperties {
    page: number;
    print: boolean,
    setPage: (page: number) => void
}

const Footer: React.FC<PropProperties> = (props: PropProperties) => {

    const { page, print, setPage } = props;

    const thumb = { 'width': '40px' };

    return (
        <>
            {
                !print ?
                    <footer className="vc--resume-main-footer col-12">
                        <div className="row">
                            <span className={`col-4 text-left ${page === 1 ? 'invisible' : ''}`}
                                onClick={() => setPage(1)}><FormattedMessage id="prev" /></span>
                            <span className="col-4 text-center invisible">&copy;{(new Date()).getFullYear()} Me <img style={thumb} className="img-reponsive img-thumbnail" src={qrCode} alt="" /></span>
                            <span className={`col-4 text-right ${page === 2 ? 'invisible' : ''}`}
                                onClick={() => setPage(2)}><FormattedMessage id="next" /></span>
                        </div >
                    </footer >
                    : ""
            }
        </>
    );
}

export default Footer;
